import React, { useRef, useEffect,useLayoutEffect, useState } from "react";
import * as d3 from "d3";
import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";
import "../Styles.scss";
import { Avatar, Modal, Tooltip } from "@mui/material";
import { Typography } from "@mui/material";
import { Box } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { plots } from "./landData";
import { Data } from "../data";
import { estates } from "./estateData";
import PQueue from "p-queue";
import { PanZoom } from "react-easy-panzoom";


function D3Grid() {
  const panning = React.useRef(false);
  const [bottomBarVisible, setBottomBarVisible] = useState("none");
  // const [bottom_modal, setBottomBarClass] = useState("show_active");
  const [show, setShow] = useState(false);
  const id = useRef(null);
  const [coordinates, setCoordinates] = useState([0, 0]);
  const [size, setSize] = useState("1x1");
  const [price, setPrice] = useState(652);
  const [owner, setOwner] = useState("");
  const [properties, setProperties] = useState([]);
  const [landData, setLandData] = useState([]);
  const [estateData, setEstateData] = useState([]);
  const [sector, setSector] = useState([]);
  const queue = new PQueue({ concurrency: 1 });
  const [id_get, setId_get] = useState("");
  const MainSquareSize = 4040;
  const plotSize = 40;
  const svgRef = useRef();
  const urlHead = "https://api-hzm-metaverse-dev.hzmgames.com";
  //const urlHead = "https://4e44-112-196-113-2.ngrok.io";
  let data = "6298bbf1c5f9f220b1dd1de2";
  const image =
    "https://raw.githubusercontent.com/andybarefoot/andybarefoot-www/master/got/images/sigil/Stark.jpg";

  useEffect(() => {
    fetch(`${urlHead}/v1/sector/list?mapId=${data}`)
      .then((response) => response.json())
      .then((data) => {
        setProperties(data);
        setSector(data.sectors);
      });
  }, []);

  useEffect(() => {
    sector.map((i) => {
      (async () => {
        await queue.add(() =>
          fetch(`${urlHead}/v1/map/list?mapId=${i.mapId}&&sectorId=${i._id}`)
            .then((response) => response.json())
            .then((data) => {
              setProperties(data);
              setLandData((prev) => [...prev, data.land]);
              setEstateData(data.estate);
            })
        );
      })();
    });
  }, [sector]);

  const getLandData = (landId) => {
    fetch(`${urlHead}/v1/land?landId=${landId}`)
      .then((response) => response.json())
      .then((res) => {
        setOwner(res.data.userData.userName);
      });
  };
  const getEstateData = (estateId) => {
    fetch(`${urlHead}/v1/estate?estateId=${estateId}`)
      .then((response) => response.json())
      .then((res) => {
        setOwner(res.data.userData.userName);
      });
  };

  console.log(sector, ">>>>>>>>SECTOR DATA");
  console.log(landData, ">>>>>>>>LAND DATA");
  console.log(estateData, ">>>>>>>>ESTATE DATA");

  function setBottomBarClass() {
    document.getElementById("BottomMenu").classList.toggle("show_active");
  }
  // Slick
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  const style = {
    display: "flex",
    flexDirection: "column",
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 1,
    height: 1,
    bgcolor: "#37383b",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
  };

  const innerBoxStyle = {
    display: "flex",
    position: "relative",
  };

  const handleClose = () => {
    setShow(false);
  };

  useLayoutEffect(() => {
    function clickHandler(i, d) {
      if (!panning.current) {
        setShow(true);
        if (d.isLandAvailable == true) {
          setBottomBarClass();
          document.getElementById("BottomMenu").style.display = "block";
          document.getElementById("BottomMenu").classList.add("show_active");
          setCoordinates([d.xCoordinate, d.yCoordinate]);
          setPrice(((d.xCoordinate * d.yCoordinate) / 2) * 2);
          getLandData(d._id);
        }
        if (d.estateType == 1) {
          setSize("3x3");
        } else if (d.estateType == 2) {
          setSize("6x6");
        } else if (d.estateType == 3) {
          setSize("12x12");
        } else {
          setSize("1x1");
        }
        d3.select(id.current)
          .style("fill", function (d) {
            if (d.isLandAvailable == true) {
              if (d.landType == 3) {
                return "#c6e053";
              } else if (d.landType == 2) {
                return "#00d2fc";
              } else {
                return "#1fac30";
              }
            } else {
              return "#141c39";
            }
          })
          .style("stroke", "black");
        d3.select(this)
          .style("fill", function (d) {
            if (d.isLandAvailable == true) {
              return "#ec8a8a";
            } else {
              return "#141c39";
            }
          })
          .style("stroke", function (d) {
            if (d.isLandAvailable == true) {
              return "#ff0000";
            } else {
              return "black";
            }
          });
        id.current = this;
      }
    }

    function EstateClickHandler(i, d) {
      if (!panning.current) {
        setShow(true);
        if (d.isLandAvailable == true) {
          setBottomBarClass();
          document.getElementById("BottomMenu").style.display = "block";
          document.getElementById("BottomMenu").classList.add("show_active");
          setCoordinates([d.xCoordinate, d.yCoordinate]);
          setPrice(((d.xCoordinate * d.yCoordinate) / 2) * 2);
          //setId_get(d._id)
          getEstateData(d._id);
        }
        if (d.estateType == 1) {
          setSize("3x3");
        } else if (d.estateType == 2) {
          setSize("6x6");
        } else if (d.estateType == 3) {
          setSize("12x12");
        } else {
          setSize("1x1");
        }
        d3.select(id.current)
          .style("fill", function (d) {
            if (d.isLandAvailable == true) {
              if (d.landType == 3) {
                return "#c6e053";
              } else if (d.landType == 2) {
                return "#00d2fc";
              } else {
                return "#1fac30";
              }
            } else {
              return "#141c39";
            }
          })
          .style("stroke", "black");
        d3.select(this)
          .style("fill", function (d) {
            if (d.isLandAvailable == true) {
              return "#ec8a8a";
            } else {
              return "#141c39";
            }
          })
          .style("stroke", function (d) {
            if (d.isLandAvailable == true) {
              return "#ff0000";
            } else {
              return "black";
            }
          });
        id.current = this;
      }
    }

    const svg = d3
      .select(svgRef.current)
      .attr("width", MainSquareSize)
      .attr("height", MainSquareSize)
      .style("background", "#172144");

    landData.map((lands) => {
      const row = svg
        .selectAll(".row")
        .data(lands)
        .enter()
        .append("rect")
        .attr("class", "square")
        .attr("id", "pic")
        .attr("x", function (d) {
          return d.xCoordinate * plotSize;
        })
        .attr("y", function (d) {
          return d.yCoordinate * plotSize;
        })
        .attr("width", plotSize)
        .attr("height", plotSize)
        .style("fill", function (d) {
          if (d.isLandAvailable == true) {
            if (d.landType == 3) {
              return "#c6e053";
            } else if (d.landType == 2) {
              return "#0fcbf1";
            } else {
              return "#1fac30";
            }
          } else {
            return "#141c39";
          }
        })
        .style("stroke", "black")
        .style("stroke-width", "3px")
        .on("mouseup", clickHandler)
        .append("title")
        .text(function (d) {
          return [d.xCoordinate, d.yCoordinate];
        });
    });

    const estate = svg
      .selectAll(".row")
      .data(estateData)
      .enter()
      .append("rect")
      .attr("class", "square")
      .attr("id", "pic")
      .attr("x", function (d) {
        return d.xCoordinate * plotSize;
      })
      .attr("y", function (d) {
        return d.yCoordinate * plotSize;
      })
      .attr("width", function (d) {
        if (d.estateType == 1) {
          return 3 * plotSize;
        } else if (d.estateType == 2) {
          return 6 * plotSize;
        } else if (d.estateType == 4) {
          return 24 * plotSize;
        } else {
          return 12 * plotSize;
        }
      })
      .attr("height", function (d) {
        if (d.estateType == 1) {
          return 3 * plotSize;
        } else if (d.estateType == 2) {
          return 6 * plotSize;
        } else if (d.estateType == 4) {
          return 24 * plotSize;
        } else {
          return 12 * plotSize;
        }
      })
      .style("fill", function (d) {
        if (d.imageUrl !== "") {
          if (d.estateType == 1) {
            return "white";
          } else if (d.estateType == 2) {
            if (
              d.imageUrl ==
              "https://d2twi13ytfv01f.cloudfront.net/upload_1654497258906.png"
            ) {
              return "white";
            } else {
              return "#553504";
            }
          } else {
            return "#553504";
          }
        } else {
          return 0;
        }
      })
      .style("stroke", "black")
      .style("stroke-width", "3px")
      .on("mouseup", EstateClickHandler)
      .append("title")
      .text(function (d) {
        return [d.xCoordinate, d.yCoordinate];
      });

    const estate_image = svg
      .selectAll(".row")
      .data(estateData)
      .enter()
      .append("image")
      .attr("xlink:href", function (d) {
        return d.imageUrl;
      })
      .attr("x", function (d) {
        return d.xCoordinate * plotSize;
      })
      .attr("y", function (d) {
        return d.yCoordinate * plotSize;
      })
      .attr("width", function (d) {
        if (d.imageUrl !== "") {
          if (d.estateType == 1) {
            return 3 * plotSize;
          } else if (d.estateType == 2) {
            return 6 * plotSize;
          } else if (d.estateType == 4) {
            return 24 * plotSize;
          } else {
            return 12 * plotSize;
          }
        } else {
          return 0;
        }
      })
      .attr("height", function (d) {
        if (d.imageUrl !== "") {
          if (d.estateType == 1) {
            return 3 * plotSize;
          } else if (d.estateType == 2) {
            return 6 * plotSize;
          } else if (d.estateType == 4) {
            return 24 * plotSize;
          } else {
            return 12 * plotSize;
          }
        } else {
          return 0;
        }
      })

      .style("padding", 10)
      .on("mouseup", EstateClickHandler);

    // const zoom = svg.call(
    //   d3
    //     .zoom()
    //     .on("zoom", zoomed)
    // );

    // function zoomed({ transform }) {
    //   svg.attr("transform", transform);
    // }
  }, [landData]);
  return (
    <>
      <header>
        <div className="container-fluid">
          <div className="d-flex align-items-center justify-content-between header_wrap">
            <a href="#" className="logo">
              <img src={require("../../images/hzm_logo.png")} alt="" />
            </a>

            <div className="d-flex align-items-center rht_hd_group ml-auto">
              <div className="coins_collected">
                Coins: <span>111</span>
              </div>

              <div className="login_pic">
                <img src={require("../../images/icons/user_icon.png")} alt="" />
              </div>
            </div>
          </div>
        </div>
      </header>

      <div className="gridMain">
        <div className="mapDiv">
          {/*  <TransformWrapper
            minScale={0.1}
            maxScale={0.9}
            limitToBounds={true}
            onPanning={() => {
              panning.current = true;
            }}
            onPanningStop={() => {
              if (panning.current) panning.current = false;
            }}
          >
            {({
              positionX,
              positionY,
              zoomIn,
              zoomOut,
              ...rest
            }) => (
              <>
                <TransformComponent>
                  <svg ref={svgRef} style={{ alignSelf: "center" }}></svg>
                </TransformComponent>
              </>
            )}
          </TransformWrapper> */}
          <PanZoom
            minZoom={0.2}
            maxZoom={0.8}
            onPanStart={() => {
              panning.current = true;
            }}
            onPanEnd={() => {
              if (panning.current) panning.current = false;
            }}
          >
            
            <svg ref={svgRef} height={4040} width={4040}>
             </svg>
            {/* <canvas ref={svgRef} height={8000} width={8000}>
            </canvas> */}
           
            
          </PanZoom>
        </div>

        <div className="bottom_modal" id="BottomMenu">
          <button className="close_btn" onClick={() => setBottomBarClass()}>
            <img src={require("../../images/icons/down_arrow.png")} alt="" />
          </button>

          <div className="bottom_menu">
            <div className="d-flex flex-column grid_btm_bar">
              <div className="block_lft">
                <div className="block_wrap">
                  <div className="block_slider">
                    <Slider {...settings}>
                      <figure className="m-0">
                        <img
                          src={require("../../images/land_modal.png")}
                          alt="Land modal"
                          className="img-fluid"
                        />
                      </figure>
                      <figure className="m-0">
                        <img
                          src={require("../../images/land_modal.png")}
                          alt="Land modal 2"
                          className="img-fluid"
                        />
                      </figure>
                      <figure className="m-0">
                        <img
                          src={require("../../images/land_modal.png")}
                          alt="Land modal 3"
                          className="img-fluid"
                        />
                      </figure>
                    </Slider>
                  </div>
                  <div className="userSummary">
                    <span className="title_user" title={"Owner of this Land"}>
                      Owned By
                    </span>
                    <div className="d-flex user_pic">
                      <Tooltip
                        title={"Owner of this Land"}
                        arrow={true}
                        placement={"bottom"}
                        enterDelay={0}
                        followCursor={true}
                      >
                        <Avatar sx={{ mr: 1, ml: 1, h: 60, w: 60 }}></Avatar>
                      </Tooltip>
                      <div className="user_info">
                        <h6> {owner}</h6>
                        <small>@{owner}.com</small>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div className="block_rht">
                <div className="d-flex block_tabs">
                  <label>
                    Location{" "}
                    <span>
                      {" "}
                      <i>
                        <img
                          src={require("../../images/icons/location_icon.png")}
                          alt=""
                        />
                      </i>
                      {coordinates[0]},{coordinates[1]}
                    </span>
                  </label>
                  <label>
                    Price{" "}
                    <span>
                      <i>
                        <img
                          src={require("../../images/camel_icon.png")}
                          alt=""
                        />
                      </i>{" "}
                      {price}
                    </span>
                  </label>
                  <label>
                    Size
                    <span>
                      <i></i> {size}
                    </span>
                  </label>
                </div>
                <div className="description_box">
                  <div className="description_wrap">
                    <h6>Description</h6>
                    <p>
                      The availability of virtual land in metaverse allows
                      buyers or renters to create monetized properties with
                      unique experiences. Also referred to as virtual land
                      parcels, the plots of real estate in the meta verse are
                      basically blocks of 3D space
                    </p>
                  </div>
                </div>
              </div>

              <div className="videoBlock">
                <div className="video_bar">
                  <iframe
                    width="560"
                    height="315"
                    src="https://www.youtube.com/embed/QJZONj_l760"
                    title="YouTube video player"
                    frameborder="0"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowfullscreen
                  ></iframe>
                </div>
              </div>

              <ul className="social_icons">
                <li>
                  <a href="#" target="_blank">
                    <img
                      src={require("../../images/icons/fb_icon.png")}
                      alt="Facebook"
                    />
                  </a>
                </li>
                <li>
                  <a href="#" target="_blank">
                    <img
                      src={require("../../images/icons/insta_icon.png")}
                      alt="Instagram"
                    />
                  </a>
                </li>
                <li>
                  <a href="#" target="_blank">
                    <img
                      src={require("../../images/icons/twitter_icon.png")}
                      alt="Twitter"
                    />
                  </a>
                </li>
              </ul>
            </div>

            <div className="videoBlock">
              <div className="video_bar">
                <iframe
                  width="560"
                  height="315"
                  src="https://www.youtube.com/embed/VopfFz9oJEM"
                  title="YouTube video player"
                  frameborder="0"
                  allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                  allowfullscreen
                ></iframe>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
export default D3Grid;
